/* https://piccalil.li/blog/a-modern-css-reset */
:root {
  --color-accent-400: hsl(12, 88%, 59%);
  --color-accent-500: hsl(12, 60%, 45%);
  --color-accent-300: hsl(12, 88%, 75%);
  --color-accent-100: hsl(13, 100%, 96%);
  --color-primary-400: hsl(228, 39%, 23%);
  --color-nuetral-900: hsl(232, 12%, 13%);
  --color-nuetral-100: hsl(0, 0%, 100%);
  --ff-primary: "Be Vietnam Pro", sans-serif;
  --ff-body: var(--ff-primary);
  --ff-heading: var(--ff-primary);
  --fw-regular: 400;
  --fw-semi-bold: 500;
  --fs-button: var(--fs-300);
  --fw-bold: 700;
  --fs-300: 0.8125rem;
  --fs-400: 0.875rem;
  --fs-500: 0.9375rem;
  --fs-600: 1rem;
  --fs-700: 1.875rem;
  --fs-800: 2.5rem;
  --fs-900: 3.5rem;
  --fs-primary-heading:var(--fs-800);
  --fs-secondary-heading:var(--fs-700)
  --fs-body: var(--fs-400);
  --fs-nav: var(--fs-500);
  -fs-button: var(--fs-300);
  --size-100:0.25rem;
  --size-200:0.50rem;
  --size-300:0.75rem;
  --size-400:1rem;
  --size-500:1.5rem;
  --size-600:2rem;
  --size-700:3rem;
  --size-800:4rem;
  --size-900:5rem;
}

@media (min-width: 50rem) {
  :root {
    --fs-700: 3.5rem;
    --fs-600: 2.5rem;
    --fs-primary-heading:var(--fs-900);
    --fs-secondary-heading:var(--fs-800);
    --fs-body: var(--fs-500);
    --fs-nav: var(--fs-300);
  }
}
/* Box sizing rules */
*,
*::before,
*::after {
  box-sizing: border-box;
}

/* Remove default margin */
* {
  margin: 0;
  padding: 0;
  font: inherit;
}

/* Remove list styles on ul, ol elements with a list role, which suggests default styling will be removed */
ul[role="list"],
ol[role="list"] {
  list-style: none;
}

/* Set core root defaults */
html:focus-within {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

/* Set core body defaults */
body {
  text-rendering: optimizeSpeed;
  line-height: 1.5;
}

/* A elements that don't have a class get default styles */
a:not([class]) {
  text-decoration-skip-ink: auto;
}

/* Make images easier to work with */
img,
picture,
svg {
  max-width: 100%;
  display: block;
}

/* Remove all animations, transitions and smooth scroll for people that prefer not to see them */
@media (prefers-reduced-motion: reduce) {
  html:focus-within {
    scroll-behavior: auto;
  }

  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
    scroll-behavior: auto !important;
  }
}

/* general styling */

body{
  font-family: var(--ff-body);
  font-size: var(--fs-body);
  color: var(--color-primary-400);
}

p:not([class]){
  opacity: 0.5;
  max-width: 32ch;
}

p[data-width='wide']{
  max-width: 100%;
}

.button{
  display: inline-flex;
  border: 0;
  padding: 1em 2.5em;
  border-radius: 100vmax;
  color: var(--color-accent-100);
  font-size: var(--fs-button);
  font-weight: var(--fw-button);
  background-color: var(--color-accent-400);
  cursor: pointer;
  line-height: 1;
  box-shadow: 0 1.125em 1em -1em var(--clr-accent-500);
}

.button:hover,.button:focus-visible{
  background-color: var(--color-accent-300);
}

.button[data-type="inverted"]{
 color: var(--color-accent-400);
 background-color: var(--color-accent-100);

}

.button[data-type="inverted"]:hover,.button[data-type="inverted"]:focus-visible{
  color: var(--color-accent-300);
  background-color: var(--color-accent-100);
}


/* navigation */

.primary-header{
  margin-top: var(--size-700);
}

.nav-wrapper{
  display:flex;
  align-items: center;
  justify-content: space-between;
}


.mobile-nav-toggle{
  display: none;
}

.nav-list{
  display: flex;
  gap: 40px;
  font-weight: var(--fw-semi-bold);
  font-size: var(--fs-nav);
}

.nav-list a{
  color:var(--color-primary-400);
  text-decoration: none;
}

.nav-list a:hover,.nav-list a:focus{
  color:var(--color-accent-400);
}

@media (max-width:50em){
  .primary-navigation{
    display: none;
    position:fixed;
    max-width: 25rem;
    margin-left: auto;
    padding: var(--size-700);
    inset: 7rem var(--size-400) auto;
    background: var(--color-nuetral-100);
    border-radius: var(--size-300);
    box-shadow: 0 0 0.75em rgb(0, 0,0.05);
  }

  .nav-list{
    display: grid;
    gap:var(--size-600);
    text-align: center;
    font-weight: var(--fw-bold);

  }
  .display-md-inline-flex{
    display: none;
  }
  .nav-list a{
    color:var(--color-primary-400);
    text-decoration: none;
  }

  .primary-header[data-overlay]::before{
    content: '';
    inset: 0;
    position:fixed;
    background:linear-gradient(rgb(0 0 0/0),rgb(0 0 0/0.8))
  }

  .nav-list a:hover,.nav-list a:focus{
    color:var(--color-accent-400);
  }

  .primary-navigation[data-visible]{
    display: block;
  }

  .mobile-nav-toggle{
    display: block;
    position: fixed;
    top: var(--size-600);
    right: var(--size-400);
    cursor: pointer;
    background: transparent;
    border:none;
    padding:0.5em;
   }

   .mobile-nav-toggle .icon-close{
    display:none
  }
}


/* utility classes */

.visually-hidden{
  position: absolute;
  width: 1px;
  height: 1px;
  padding: 0;
  margin: -1px;
  border: 0;
  overflow: hidden;
  clip: rect(0,0,0,0);
  white-space: nowrap;
}

:where(.flow :not(:first-child)){
  margin-top: var(--flow-spaccer,3em);

}

.container{
  --max-width:1110px ;
  --padding:1rem ;
  width: min(var(--max-width),100% - (var(--padding)*2));
  margin-inline: auto;
}

.even-columns{
  display: grid;
  gap: 1rem;
}

@media(min-width:50em){
  .even-columns{
    grid-auto-flow: column;
    grid-auto-columns: 1fr;
  }
}

